import { Injectable } from '@nestjs/common';

import {
  ProgressCheckReport,
  ProjectBudgetChange,
  ProjectCloseReport,
  ProjectCommonReport,
  ProjectMeetingReport,
  ProjectStatusUpdateReport,
  ProjectWorkerReport,
  UpdateProgressCheckReport,
  UpdateProjectBudgetChange,
  UpdateProjectCloseReport,
  UpdateProjectCommonReport,
  UpdateProjectMeetingReport,
  UpdateProjectStatusUpdateReport,
  UpdateProjectWorkerReport,
} from '../entity';
import { ProjectReportId } from '../value';

@Injectable()
export abstract class ReportRepository {
  abstract createProgressCheckReport(
    report: ProgressCheckReport,
  ): Promise<ProgressCheckReport>;
  abstract createProjectBudgetChange(
    report: ProjectBudgetChange,
  ): Promise<ProjectBudgetChange>;
  abstract createProjectCloseReport(
    report: ProjectCloseReport,
  ): Promise<ProjectCloseReport>;
  abstract createProjectCommonReport(
    report: ProjectCommonReport,
  ): Promise<ProjectCommonReport>;
  abstract createProjectMeetingReport(
    report: ProjectMeetingReport,
  ): Promise<ProjectMeetingReport>;
  abstract createProjectStatusUpdateReport(
    report: ProjectStatusUpdateReport,
  ): Promise<ProjectStatusUpdateReport>;
  abstract createProjectWorkerReport(
    report: ProjectWorkerReport,
  ): Promise<ProjectWorkerReport>;

  abstract updateProgressCheckReport(
    updateReport: UpdateProgressCheckReport,
  ): Promise<ProgressCheckReport>;
  abstract updateProjectBudgetChange(
    updateReport: UpdateProjectBudgetChange,
  ): Promise<ProjectBudgetChange>;
  abstract updateProjectCloseReport(
    updateReport: UpdateProjectCloseReport,
  ): Promise<ProjectCloseReport>;
  abstract updateProjectCommonReport(
    updateReport: UpdateProjectCommonReport,
  ): Promise<ProjectCommonReport>;
  abstract updateProjectMeetingReport(
    updateReport: UpdateProjectMeetingReport,
  ): Promise<ProjectMeetingReport>;
  abstract updateProjectStatusUpdateReport(
    updateReport: UpdateProjectStatusUpdateReport,
  ): Promise<ProjectStatusUpdateReport>;
  abstract updateProjectWorkerReport(
    updateReport: UpdateProjectWorkerReport,
  ): Promise<ProjectWorkerReport>;

  abstract getProgressCheckReportById(
    id: ProjectReportId,
  ): Promise<ProgressCheckReport>;
  abstract getProjectBudgetChangeById(
    id: ProjectReportId,
  ): Promise<ProjectBudgetChange>;
  abstract getProjectCloseReportById(
    id: ProjectReportId,
  ): Promise<ProjectCloseReport>;
  abstract getProjectCommonReportById(
    id: ProjectReportId,
  ): Promise<ProjectCommonReport>;
  abstract getProjectMeetingReportById(
    id: ProjectReportId,
  ): Promise<ProjectMeetingReport>;
  abstract getProjectStatusUpdateReportById(
    id: ProjectReportId,
  ): Promise<ProjectStatusUpdateReport>;
  abstract getProjectWorkerReportById(
    id: ProjectReportId,
  ): Promise<ProjectWorkerReport>;
}
